import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Status, NewCase, StateStatus, State, District, Nation } from 'src/app/models/status.model';
import { CovidService } from './services/covid.service';
import { AppError } from '../Errors/AppError';
import { NotFoundError } from '../Errors/not-found-error';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit {

  states;
  districts: District[];
  totalStatus : Status;
  viewMode = "total";
  mainViewMode = "india";
  consolidatedViewMode = 'india-total';
  nationStatus : Nation;
  stateSelected : State; 

  constructor(private covidService : CovidService) {
    
   }

  ngOnInit(): void {
    this.covidService.getConsolidateData()
      .subscribe(response => {
        console.log(response);
        this.nationStatus = response;
      },
      (error : AppError) => {
        if(error instanceof NotFoundError)
          console.log(error);
        else
          throw error;
      })
  }

  getStateStatus(stateCode : string){
    this.stateSelected = this.nationStatus.states.find(x => x.stateCode === stateCode);
    console.log(this.stateSelected);
    this.stateSelected.districts.sort((a, b): number => this.getSorted(a, b));
    this.totalStatus = {
      active : this.getTotalActiveCases(),
      deceased : this.getTotalDeceasedCases(),
      recovered : this.getTotalRecoveredCases(),
      total : this.getTotalCases()
    };
  }  

  getSorted(a, b){
    if (a.status.total < b.status.total) return 1;
    if (a.status.total > b.status.total) return -1;
    return 0;
  }

  getConsolidatedDataSorted(a, b){
    if (a.status.total < b.status.total) return 1;
    if (a.status.total > b.status.total) return -1;
    return 0;
  }

  getTotalCases(): number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.status.total);
    }
    return total;
  }

  getTotalActiveCases(): number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.status.active);
    }
    console.log(total);
    return total;
  }

  getTotalRecoveredCases(): number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.status.recovered);
    }
    console.log(total);
    return total;
  }

  getTotalDeceasedCases(): number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.status.deceased);
    }
    return total;
  }

  getTodayConfirmedCases() : number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.newStatus.confirmed);
    }
    return total;
  }

  getTodayDeceasedCases() : number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.newStatus.deceased);
    }
    return total;
  }

  getTodayRecoveredCases() : number{
    var total = 0;
    if(this.stateSelected.districts != null && this.stateSelected.districts.length > 0){
      this.stateSelected.districts.forEach(x => total += x.newStatus.recovered);
    }
    return total;
  }

  changeViewMode(mode : string){
    this.viewMode = mode;
  }

  changeMainViewMode(mode : string){
    this.mainViewMode = mode;
    if(mode === 'state'){
      this.getStateStatus('KL');
    }
  }

  changeMainDayMode(mode : string){
    this.consolidatedViewMode = mode;
  }
}


