import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterStates'
})
export class FilterStatesPipe implements PipeTransform {

  transform(states: any[], filter: any): any {
    if (!states || !filter)
      return states;
    
    return states.filter(state => state.stateCode == filter.stateCode);
  }

}
